
import Toast from 'vux/src/components/toast/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

export default {
  components: {
    Group,
    XInput,
    XButton,
    Toast
  },
  data () {
    return {
      codeTxt: '',
      timer: 60,       //30
      stop : false,   //false
      Interval:null,  //setInterval
      tips:"",
      showPositionValue:false,
      mobile:'',
      code:'',
      bindmobile:false
    }
  },
  mounted(){
    this.$http({
            method:'POST',
            url:this.$store.state.domain+'api/WechatAPI/IsBindCustomerPhone',
            body:{},
            headers: {"Authorization": this.$store.state.jwt.token}
            })
        .then((d) => {
                if(!d.data.success){
                  this.showPosition(d.data.message);
                  return false;
                }else{
                  if(d.data.content){
                    this.$router.push("/info")
                  }else{
                    this.bindmobile=true;
                  }
                }
              }, (d) => {
                console.log('');
        });
  },
  methods: {
    showPosition (t) {
      this.showPositionValue = true
      this.tips=t;
    },
    onSubmit(ev) {
       if(!this.mobile){
       this.showPosition("");
       return false;
       }
       this.$http({
                    method:'POST',
                    url:this.$store.state.domain+'api/WechatAPI/BindCustomerPhone',
                    body:{'phone':this.mobile,'code':this.code},
                    headers: {"Authorization": this.$store.state.jwt.token}
                    })
        .then((d) => {
                if(!d.data.success){
                  this.showPosition(d.data.message);
                  return false;
                }else{
                  this.$router.push("/info");
                }
              }, (d) => {
                this.showPosition('');
        });
      
    },
    update () {
        if(this.timer <= 0) 
        {
            this.timer = 60;
            this.codeTxt="";
            clearInterval(this.Interval);
            this.stop = !this.stop;
        }
        else{
            this.timer--;
            this.codeTxt=this.timer+"S";
        }
    },
    startTimer () {
      if(!this.mobile){
        this.showPosition("");
        return false;
      }
        this.$http({
                    method:'POST',
                    url:this.$store.state.domain+'api/WechatAPI/SendSmsForBindCustomerPhone',
                    body:{'phone':this.mobile},
                    headers: {"Authorization": this.$store.state.jwt.token}
                    })
        .then((d) => {
                if(!d.data.success){
                  this.showPosition(d.data.message);
                  return false;
                }else{
                  //falsetrue
                    if(!this.stop)
                    {
                      this.stop = !this.stop;
                      this.Interval = setInterval(this.update,1000);    
                    }
                }
              }, (d) => {
                console.log('');
        });

        

        //this.stop = !this.stop;
    }
  }
}



